import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    intersectingCard: undefined,
    appbarProperties: {
      color: "transparent",
      dark: true,
      elevation: 0,
      intersectingCard: undefined,
    },
  },
  mutations: {
    async setAppBar(state, payload) {
      state.appbarProperties = payload.token;
    },
  },
  actions: {
    setAppBar(context, payload) {
      context.commit("setAppBar", payload);
    },
  },
  modules: {},
});
