<script>
import About from "./views/About.vue";
import Education from "./views/Education.vue";
import Home from "./views/Home.vue";
import Skills from "./views/Skills.vue";
import Experience from "./views/Experience.vue";
import Contact from "./views/Contact.vue";
import Projects from "./views/Projects.vue";

export default {
  name: "App",
  components: {
    About,
    Education,
    Skills,
    Home,
    Experience,
    Projects,
    Contact,
  },

  data: () => ({}),
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      console.log(window.scrollY);
      if (window.scrollY < 955) {
        this.$store.dispatch("setAppBar", {
          token: {
            color: "transparent",
            dark: true,
            elevation: 0,
            intersectingCard: 0,
          },
        });
      } else if (955 <= window.scrollY && window.scrollY < 1456) {
        this.$store.dispatch("setAppBar", {
          token: {
            color: "white",
            dark: false,
            elevation: 8,
            intersectingCard: 1,
          },
        });
      } else if (1456 <= window.scrollY && window.scrollY < 2237) {
        this.$store.dispatch("setAppBar", {
          token: {
            color: "white",
            dark: false,
            elevation: 8,
            intersectingCard: 2,
          },
        });
      } else if (2237 <= window.scrollY && window.scrollY < 2899) {
        this.$store.dispatch("setAppBar", {
          token: {
            color: "white",
            dark: false,
            elevation: 8,
            intersectingCard: 3,
          },
        });
      } else if (2899 <= window.scrollY && window.scrollY < 3508) {
        this.$store.dispatch("setAppBar", {
          token: {
            color: "white",
            dark: false,
            elevation: 8,
            intersectingCard: 4,
          },
        });
      } else {
        this.$store.dispatch("setAppBar", {
          token: {
            color: "white",
            dark: false,
            elevation: 8,
            intersectingCard: 5,
          },
        });
      }
    },
  },
};
</script>
